/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #fafafa;
  color: #707070;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #ff8300;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #707070;
  font-size: 2rem;
}

.nombre-pagina {
  text-align: center;
  margin: 5rem 0;
  font-weight: 700;
}

.descripcion-pagina {
  text-align: center;
}

.app {
  width: 85%;
  margin: 0 auto;
  padding: 3rem 0;
}

.seccion {
  padding: 2rem 0;
}
.seccion .text-center {
  text-align: center;
}

.header {
  background-color: #ff8300;
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #333;
  }
}
.header .contenido-header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

@media (min-width: 768px) {
  .navegacion-principal {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 2rem;
  }
}
.navegacion-principal a {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  text-align: center;
  color: #fafafa;
  font-size: 2.2rem;
  transition: 0.3s ease;
  font-weight: 700;
}
@media (min-width: 768px) {
  .navegacion-principal a {
    margin: 0;
  }
}
.navegacion-principal a:hover {
  color: #1a1b15;
}
.navegacion-principal i {
  font-size: 3.2rem;
}

.seccion-inicio {
  padding: 5rem 0;
  display: none;
}
.seccion-inicio .text-center {
  text-align: center;
}

.mostrar {
  display: block;
}

.ocultar {
  display: none;
}

.listado-promotores {
  display: grid;
  gap: 2rem;
}
@media (min-width: 480px) {
  .listado-promotores {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 768px) {
  .listado-promotores {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-promotores {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
.listado-promotores a {
  transition-property: transform;
  transition-duration: 300ms;
}
.listado-promotores a:hover {
  transform: scale(1.1);
}
.listado-promotores p {
  font-size: 1.8rem;
  transition: all 0.2s ease;
  text-align: left;
  cursor: pointer;
}
.listado-promotores p:hover {
  color: #ff8300;
}
.listado-promotores .promotor {
  border-bottom: solid 1px #707070;
}

.applogin {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 90rem;
}

.wrapper {
  position: relative;
  width: 65rem;
  height: 62rem;
  margin: 0 2rem 0 2rem;
  background-color: transparent;
  border: 2px solid #ff8300;
  border-radius: 2rem;
  overflow: hidden;
}
@media (min-width: 768px) {
  .wrapper {
    width: 75rem;
    height: 55rem;
    margin: none;
  }
}
.wrapper .form-box {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .wrapper .form-box {
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    margin: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}
.wrapper .form-box img {
  width: 16rem;
  margin-bottom: -2rem;
}
.wrapper .form-box h2 {
  font-size: 3.2rem;
  margin: 0 auto;
  padding-bottom: 2rem;
}
.wrapper .form-box.login {
  padding: 14rem 4rem;
}
@media (min-width: 768px) {
  .wrapper .form-box.login {
    left: 0;
    padding: 0 6rem 0 4rem;
  }
}
.wrapper .form-box.login .animation {
  transform: translateX(0);
  opacity: 1;
  filter: blur(0);
  transition: 0.5s ease;
  transition-delay: calc(0.04s * var(--j));
}
.wrapper .input-box {
  position: relative;
  width: 100%;
  height: 5rem;
  margin: 2.5rem 0;
}
.wrapper .input-box input {
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: none;
  outline: none;
  border-bottom: 2px solid #707070;
  padding-right: 2.3rem;
  font-size: 1.6rem;
  color: #707070;
  font-weight: 500;
  transition: 0.5s;
}
.wrapper .input-box input:focus,
.wrapper .input-box input:valid {
  border-bottom-color: #ff8300;
}
.wrapper .input-box label {
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  font-size: 1.6rem;
  color: #707070;
  pointer-events: none;
  transition: 0.5s;
}
.wrapper .input-box input:focus ~ label,
.wrapper .input-box input:valid ~ label {
  top: -0.5rem;
  color: #ff8300;
}
.wrapper .input-box i {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  font-size: 1.8rem;
  color: #707070;
  transition: 0.5s;
}
.wrapper .input-box input:focus ~ i,
.wrapper .input-box input:valid ~ i {
  color: #ff8300;
}
.wrapper .info-text {
  position: absolute;
  top: 0;
  width: 100%;
  height: 20%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .wrapper .info-text {
    width: 65%;
    height: 100%;
  }
}
.wrapper .info-text .poscion {
  position: absolute;
  top: 10%;
  left: 0;
  width: 100%;
}
@media (min-width: 768px) {
  .wrapper .info-text .poscion {
    top: 35%;
    left: 50%;
    width: 50%;
  }
}
.wrapper .info-text h2, .wrapper .info-text h3 {
  font-size: 2.4rem;
  color: #fafafa;
  line-height: 1.3;
  text-transform: uppercase;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .wrapper .info-text h2, .wrapper .info-text h3 {
    font-size: 3rem;
  }
}
.wrapper .info-text h3 {
  font-size: 2rem;
}
@media (min-width: 768px) {
  .wrapper .info-text h3 {
    font-size: 2.4rem;
    padding-bottom: 0;
  }
}
.wrapper .info-text p {
  font-size: 1.6rem;
  color: #fafafa;
  line-height: 1.3;
  border-top: solid 2px #fafafa;
  border-bottom: solid 2px #fafafa;
  text-align: center;
}
@media (min-width: 768px) {
  .wrapper .info-text p {
    font-size: 1.8rem;
  }
}
.wrapper .info-text.login {
  text-align: center;
  background: linear-gradient(142deg, #ffba4b 18%, #ff8300 82%);
  border-bottom: none;
}
@media (min-width: 768px) {
  .wrapper .info-text.login {
    right: 2.5rem;
    text-align: right;
    background: none;
    border-bottom: none;
    padding: 0 2rem 8rem 15rem;
  }
}
.wrapper .info-text.login .animation {
  transform: translateX(0);
  opacity: 1;
  filter: blur(0);
  transition: 0.5s ease;
  transition-delay: calc(0.04s * var(--j));
}
@media (min-width: 768px) {
  .wrapper .bg-animate {
    position: absolute;
    top: -0.4rem;
    right: 0;
    width: 85rem;
    height: 65rem;
    background: linear-gradient(142deg, #ffba4b 18%, #ff8300 82%);
    border-bottom: none;
    transform: rotate(10deg) skewY(40deg);
    transform-origin: bottom right;
    transition: 0.5s ease;
    transition-delay: 0.6s;
  }
}

.btn {
  background: linear-gradient(142deg, #ffba4b 18%, #ff8300 82%);
  color: #fafafa;
  font-size: 1.6rem;
  font-weight: 700;
  border: none;
  outline: none;
  transition: all 0.2s ease;
  text-align: center;
  width: 100%;
  height: 4.5rem;
  border-radius: 4rem;
  cursor: pointer;
  z-index: 1;
  overflow: hidden;
}
.btn:hover {
  scale: 1.05;
}
.btn:active {
  scale: 0.95;
}

.abajo {
  color: #ffba4b;
  font-size: 4rem;
  display: flex;
  position: fixed;
  left: 86%;
  top: 94%;
}
@media (min-width: 480px) {
  .abajo {
    color: #ff8300;
    font-size: 5rem;
    left: 92.8%;
    top: 90%;
  }
}

.botones-arriba {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.botones-arriba .arriba {
  color: #ff8300;
  font-size: 4rem;
}
@media (min-width: 480px) {
  .botones-arriba .arriba {
    color: #ffba4b;
    font-size: 3.5rem;
  }
}
.botones-arriba .back {
  color: #ffba4b;
  font-size: 4rem;
}
@media (min-width: 480px) {
  .botones-arriba .back {
    color: #ff8300;
    font-size: 3.5rem;
  }
}

.alerta {
  padding: 1rem;
  width: 100%;
  display: block;
  margin-bottom: 1rem;
  text-align: center;
  font-weight: 700;
  font-size: 1.2rem;
}
.alerta.error {
  background-color: #ffb3b3;
  color: #cb0000;
}
.alerta.exito {
  background-color: #ceffb3;
  color: #2b8700;
}

@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}
.tabs button {
  background-color: #fafafa;
  border: none;
  color: #ff8300;
  padding: 1rem;
  display: block;
  width: 100%;
  margin-bottom: 1rem;
  font-weight: 700;
  transition: background-color 300ms ease;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #ededed;
}
.tabs button.actual {
  background: linear-gradient(142deg, #ffba4b 18%, #ff8300 82%);
  color: #fafafa;
}

.contenedor-buscador {
  position: relative;
  display: flex;
  gap: 1rem;
  align-items: center;
}
.contenedor-buscador .buscador {
  background: transparent;
  border: none;
  color: #ff8300;
  display: inline-block;
  font-size: 3.5rem;
  padding: 2rem 0;
  outline: none;
  transition: width 0.4s;
  z-index: 3;
}
.contenedor-buscador .input-buscador {
  overflow: visible;
  outline: none;
  width: 16rem;
  background: transparent;
  border: none;
  color: #707070;
  font-size: 1.8rem;
  transition: 0.1s ease;
}
.contenedor-buscador .input-buscador:focus {
  border-bottom: solid 1.5px #ff8300;
}

.filter {
  display: none;
}

.desborde {
  display: block;
  overflow-x: auto;
}

.tabla {
  margin-top: 2rem;
  width: 100%;
  border-spacing: 0;
  border-collapse: collapse;
}
.tabla thead {
  background: linear-gradient(142deg, #ffba4b 18%, #ff8300 82%);
}
.tabla thead th {
  color: #fafafa;
  padding: 2rem;
  text-transform: uppercase;
}
.tabla tbody tr:nth-child(odd) {
  background-color: #fafafa;
}
.tabla tbody tr:nth-child(even) {
  background-color: #f1f1f1;
}
.tabla tbody tr, .tabla tbody td {
  font-size: 1.4rem;
  font-weight: 400;
  letter-spacing: 0.35;
  color: #1a1b15;
  padding: 2rem;
  text-align: center;
  transition: all 0.3s ease;
}
.tabla tbody tr:hover {
  background-color: #ffe6cc;
}
.tabla tbody td:hover {
  color: #ff8300;
}